import React, { Component } from 'react'
import styled from 'styled-components'
import {quote} from './utils/dbquote'
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {faTwitterSquare} from '@fortawesome/free-brands-svg-icons'
const Wrapper = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: auto;
    margin-left:  auto;
    max-width: 960px;
    padding-right: 10px;
    padding-left:  10px;
    background: aquamarine;
    &:hover #newquote { 
        border: 2px solid #cf0000;
        color: #cf0000;
    }
`

const Box = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 10px;
    max-width: 500px;
    background: white;
`

const BoxTwo = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-right: auto;
    margin-left:  auto;
    border-radius: 15px;
`

const Button = styled.button`
  display: inline-block;
  color: #02475e;
  font-size: 1em;
  margin: 1em;
  padding: 0.25em 1em;
  border: 2px solid #02475e;
  border-radius: 3px;
  display: block;
`
const Ancle = styled.a`
    font-size: 1.8em;
    color: #02475e;
    &:hover { 
        color: #cf0000;
    }
`

var i = 0;
/*const rand = () => {
    var i = Math.round(Math.random(0, quote.length))
}*/

export default class Randomgen extends Component {

    constructor() {
        super();
        this.state = {
            text: quote[i].text,
            author: quote[i].author,
            tweet: 'https://twitter.com/intent/tweet?text=Un%20Fabuloso%20Random%20Quote%20Genarator!!&url=https%3A%2F%2Fj-roma.github.io%2Fquote-generator%2Ft'
        }
    }

    render() {
        return (
            <Wrapper id="wrapper">
            <Box id="quote-box">
                <div id="text">
                    <p>{this.state.text}</p>
                </div>
                <div id="author">
                    <p>- {this.state.author}</p>

                </div>
                <BoxTwo id="new-quote">
                    <Ancle id="tweet-quote" ><FontAwesomeIcon icon={faTwitterSquare} /></Ancle>
                    <Button id="newquote" onClick={() => {
                        let origin = i
                        i = Math.floor(Math.random()*quote.length)
                        origin === i ? i = Math.floor(Math.random()*quote.length) : this.setState({text:quote[i].text, author: quote[i].author}); 
                        console.log(i)
                        this.setState({text:quote[i].text, author: quote[i].author})
                        }}>Random Quote</Button>
                </BoxTwo>

            </Box>
        </Wrapper>
        )
    }
}



